import React from "react";
import { graphql, Link } from "gatsby";
import { ContentLayoutWithSidebar } from "../../components/Layout/ContentLayout";
import { SideBar } from "../../components/SideBar/SideBar";
import { SideBarQuickstart } from "../../components/SideBar/SideBarQuickstart";
import { SideBarHelp } from "../../components/SideBar/SideBarHelp";
import { CategoryBar } from "../../components/CategoryBar/CategoryBar";
import { IHeaderProps } from "../../components/Layout/Header";
import { SearchInput } from "../../components/Search/SearchInput";
import { IGatsbyLocation } from "../../interfaces";
import "./NotFound.sass"

interface IQueryResult 
{
  data: any;
  context: any
  location: IGatsbyLocation;
}
export class NotFoundPage extends React.Component<IQueryResult>
{
  render() 
  {
    let categories = this.props.data.categories.edges.map(e => e.node);

    let header: IHeaderProps = {
      url: "404",
      description: `404 - Not Found`,
      type: "object",
      title: `Durchblick durch Daten - 404 Not Found`,
      tags: ["404", "Not Found"],
    }
    return(
      <ContentLayoutWithSidebar 
        header={header}
        sidebar={(
          <SideBar isFixed={false}>
            <SideBarQuickstart/>
            <SideBarHelp/>
            {/* <SideBarCategories/> */}
          </SideBar> 
        )} 
        >
      <CategoryBar title={"Hier scheint nichts zu sein"}/>
      <div>
        Oops, diese Seite existiert nicht. Falls Sie nach etwas Bestimmten suchen, probieren Sie es mit unserer Artikelsuche:
        <div className="center-el">
          <SearchInput></SearchInput>
        </div>
        Falls Sie einen toten Link auf unserer Seite entdeckt haben, nehmen Sie gerne Kontakt mit uns auf und geben Sie uns bescheid.
        <div className="center-el">
          <Link to="/kontakt" className="button action-button">Kontakt aufnehmen</Link>
        </div>
      </div>
      </ContentLayoutWithSidebar>
    )
  }
}


const Page = ({data, pageContext, location}) => {
  return (
    <NotFoundPage data={data} context={pageContext} location={location}></NotFoundPage>
  );
};

export default Page;

export const pageQuery = graphql`
  {
    ...categories
  }
`;
